import React from 'react'
import { useMediaQuery } from 'react-responsive'
import Flipcard1 from './Flipcard1'
import brand from '../assets/img/brand/sk-sail.jpg'
import code from '../assets/img/lovecode.jpg'
import learn from '../assets/img/learning.jpg'
import office from '../assets/img/office.png'
import tranc from '../assets/img/tranc1.jpg'
import social from '../assets/img/social.jpg'
import stockholms from '../assets/img/Stockholms.png'

export const Career = () => {
    const tit = ['We love to code' , 'Office' , 'Social' , 'Learning' , 'Transparency' , 'Stockholm'];
    const par = [ "Everyone at the company loves software development. No boring lunches when everyone is having a heated debate on the latest technologies.",
                    "We have an awesome office in the absolute center of Stockholm with lots of great lunch restaurants and bars nearby.",
                    "We enjoy hanging out together, share a beer at the office or watch the latest sci-fi movie. Many of us are also happy gamers and love to play a match of CS:GO or LoL at the end of the work day.",
                    "We have our own Tech Talks and other inspiration events to boost your tech skills. We also try to make sure our developers can attend awesome conferences and meet-ups!",
                    "We have a flat hierarchy and believe in sharing information with everyone. Our salary model is transparent and beneficial for developers. We pride ourselves on providing a lot of individual freedom.",
                    "Sweden and Stockholm is consistently recognised as one of the world's best places to live. Adding to that, Stockholm has a very innovative IT-industry and a vibrant start-up scene.",
                ] 

                const Desktop = ({ children }) => {
                    const isDesktop = useMediaQuery({ minWidth: 992 })
                    return isDesktop ? children : null
                  }
                  const Tablet = ({ children }) => {
                    const isTablet = useMediaQuery({ minWidth: 768, maxWidth: 991 })
                    return isTablet ? children : null
                  }
                  const Mobile = ({ children }) => {
                    const isMobile = useMediaQuery({ maxWidth: 991 })
                    return isMobile ? children : null
                  }
                  const Default = ({ children }) => {
                    const isNotMobile = useMediaQuery({ minWidth: 768 })
                    return isNotMobile ? children : null
                  }
                
    return ( 
        <div className="cusContainer" id="Careers">
            <h1>Careers</h1>
            <h4>At Slagkryssaren the best minds are at play, whatever the current. Come sail with us!</h4>
            <div>
                <Default>
                <img src={brand} style={{ width: "100%", height: "400px" }} alt="" />
                </Default>
                <Mobile>
                <img src={brand} style={{ width: "100%", height: "200px" }} alt="" />
                </Mobile>
                <p style={{ backgroundColor: "#000", color: "#fff", padding: "16px" }}>
                    Slagkryssaren is a company for developers by developers. We understand the importance of taking care of our talented employees and to challenge them with new exciting projects. We strongly believe in creating a good work environment where everyone feel comfortable and welcome.
                </p>
            </div>

            <div>
            <Flipcard1 title = {tit[0]} img = {code} par ={par[0]}/>
            <Flipcard1 title = {tit[1]} img = {office} par ={par[1]}/>
            <Flipcard1 title = {tit[2]} img = {social} par ={par[2]}/>
            <br/>
            <Flipcard1 title = {tit[3]} img = {learn} par ={par[3]}/>
            <Flipcard1 title = {tit[4]} img = {tranc} par ={par[4]}/>
            <Flipcard1 title = {tit[5]} img = {stockholms} par ={par[5]}/>
            </div>
            <button className="btn btn-warning" style={{width:"80%"}}> Apply Now </button>



        </div>
    )
}
export default Career