import React from 'react'
import { useMediaQuery } from 'react-responsive'
export const Card = (props) => {
    const img = (props.img)?props.img : ""
    const h1 = (props.h1)?props.h1:""
    const h2 = (props.h2)?props.h2:""
    const h3 = (props.h3)?props.h3:""
    const p1 = (props.p1)?props.p1:""
    const p2 = (props.p2)?props.p2:""
    const p3 = (props.p3)?props.p3:""
    const close = (props.close)?props.close:""
    const Desktop = ({ children }) => {
        const isDesktop = useMediaQuery({ minWidth: 992 })
        return isDesktop ? children : null
      }
      const Tablet = ({ children }) => {
        const isTablet = useMediaQuery({ minWidth: 768, maxWidth: 991 })
        return isTablet ? children : null
      }
      const Mobile = ({ children }) => {
        const isMobile = useMediaQuery({ maxWidth: 991 })
        return isMobile ? children : null
      }
      const Default = ({ children }) => {
        const isNotMobile = useMediaQuery({ minWidth: 768 })
        return isNotMobile ? children : null
      }
    return (
        <>
        <Default>
        <div className="text-center" style={{ height:"600px",width:"100%" , overflow:"scroll" , paddingTop:"8px" }}>
            <div style={{paddingTop:"10%"}}>
                    <img src={img} alt="" style={{width:"100%" ,height:"400px" ,padding:"32px"}}/>
                    <h3>{h1}</h3>
                    <p style={{textAlign:"left" , fontSize:"1em"}}>{p1}</p>
                    <h3>{h2}</h3>
                    <p style={{textAlign:"left" , fontSize:"1em"}}>{p2}</p>
                    <h3>{h3}</h3>
                    <p style={{textAlign:"left" , fontSize:"1em"}}>{p3}</p>
                    <button className="btn btn-danger text-center" onClick={close} style={{marginTop:"5%"}}>Close</button>
            </div>
        </div>
        </Default>
        <Mobile>
        <div style={{ width:"400px" , height:"600px",  overflow:"scroll" , paddingTop:"8px" }}>
            <div style={{paddingTop:"10%"}}>
                    <img src={img} alt="" style={{height:"200px"}}/>
                    <h3>{h1}</h3>
                    <p style={{textAlign:"left" , fontSize:"1em"}}>{p1}</p>
                    <h3>{h2}</h3>
                    <p style={{textAlign:"left" , fontSize:"1em"}}>{p2}</p>
                    <h3>{h3}</h3>
                    <p style={{textAlign:"left" , fontSize:"1em"}}>{p3}</p>
                    <button className="btn btn-danger text-center" onClick={close} style={{marginTop:"5%"}}>Close</button>
            </div>
        </div>
        </Mobile>
        </>
    )
}
export default Card