import React from 'react'
import { useMediaQuery } from 'react-responsive'

import img1 from '../assets/img/clients/bosch-logo@2x.png'
import img2 from '../assets/img/clients/camfil-logo@2x.jpg'
import img3 from '../assets/img/clients/cineasterna-logo@2x.png'
import img4 from '../assets/img/clients/heidelberg-logo.png'
import img5 from '../assets/img/clients/mz-logo@2x.png'
import img6 from '../assets/img/clients/nordnet-logo.png'
import img7 from '../assets/img/clients/sas-logo@2x.png'
import img8 from '../assets/img/clients/scrive-logo@2x.png'
import img9 from '../assets/img/clients/spotify-logo.png'
import img10 from '../assets/img/clients/thomascook-logo.png'
import img11 from '../assets/img/clients/ving-logo@2x.png'



export const Clients = () => {
    const Desktop = ({ children }) => {
        const isDesktop = useMediaQuery({ minWidth: 992 })
        return isDesktop ? children : null
      }
      const Tablet = ({ children }) => {
        const isTablet = useMediaQuery({ minWidth: 768, maxWidth: 991 })
        return isTablet ? children : null
      }
      const Mobile = ({ children }) => {
        const isMobile = useMediaQuery({ maxWidth: 991 })
        return isMobile ? children : null
      }
      const Default = ({ children }) => {
        const isNotMobile = useMediaQuery({ minWidth: 768 })
        return isNotMobile ? children : null
      }
   


    return (
        <div className="cusContainer" style={{background:"#fff" }} id="Clients">
          <h1>Clients</h1>
            <div className="row" >
                <div className="col-xl-2 col-lg-2 col-md-2 col-sm-2">
                    <h3>What matters most to us</h3>
                    <p style={{textAlign:"left"}}>We help you create the custom software solution that brings most value to your business. We cover any parts needed to build a world class digital service that can strengthen your competitiveness or simply save time and cost.</p>
                </div>
                
                <Default>
                <div className="col-xl-10 col-lg-10 col-md-10 col-sm-10">
                    <img src={img1} className="swicon" alt=""/>
                    <img src={img2} className="swicon" alt=""/>
                    <img src={img3} className="swicon" alt=""/>
                    <img src={img4} className="swicon" alt=""/>
                    <img src={img6} className="scicon" alt=""/>
                    <br/>
                    
                    <img src={img7} className="swicon" alt=""/>
                    <img src={img8} className="swicon" alt=""/>
                    <img src={img9} className="swicon" alt=""/> 
                    <img src={img11} className="scicon" alt=""/>
                    <img src={img1} className="swicon" alt=""/>

                    <br/>
                    <img src={img5} className="swicon" alt=""/>
                    <img src={img10} className="swicon" alt=""/>
                    <img src={img1} className="swicon" alt=""/>
                    <img src={img1} className="swicon" alt=""/>
                    <img src={img1} className="swicon" alt=""/>     
                </div>                        
                </Default>
                <Mobile>
                <div className="col-xl-10 col-lg-10 col-md-10 col-sm-10" >
                    <img src={img1} className="swicon1" alt=""/>
                    <img src={img2} className="swicon1" alt=""/>
                    <img src={img3} className="swicon1" alt=""/>
                    </div>
                    <div className="col-xl-10 col-lg-10 col-md-10 col-sm-10" >
                    <img src={img4} className="swicon1" alt=""/>
                    <img src={img6} className="scicon1" alt=""/>
                    <img src={img7} className="swicon1" alt=""/>
                    </div>

                    <div className="col-xl-10 col-lg-10 col-md-10 col-sm-10" >
                    <img src={img8} className="swicon1" alt=""/>
                    <img src={img9} className="swicon1" alt=""/> 
                    <img src={img11} className="scicon1" alt=""/>
                    </div>
                    
                    <div className="col-xl-10 col-lg-10 col-md-10 col-sm-10">
                    <img src={img1} className="swicon1" alt=""/>
                    <img src={img5} className="swicon1" alt=""/>
                    <img src={img10} className="swicon1" alt=""/>
                    </div>
                    <div className="col-xl-10 col-lg-10 col-md-10 col-sm-10">
                    <img src={img1} className="swicon1" alt=""/>
                    <img src={img1} className="swicon1" alt=""/>
                    <img src={img1} className="swicon1" alt=""/>     
                </div>                         
                </Mobile>
            </div>
            
            
        </div>
    )
}
export default Clients