import React from 'react'
import Flippy, { FrontSide, BackSide } from 'react-flippy';
export const Flipcard = (props) => {
    const title = (props.title) ? props.title : "title"
    const img = (props.img) ? props.img : ""
    const par = (props.par) ? props.par : "hello"
    return (
        <>
            <Flippy
                flipOnHover={true} // default false
                flipOnClick={false} // default false
                flipDirection="horizontal" // horizontal or vertical
                style={{ width: '300px', height: '400px' ,display:"inline-block" , padding : "8px"}} /// these are optional style, it is not necessary
            >
                <FrontSide
                    style={{
                        padding: "0px",
                        margin: "0px",
                        borderRadius: "10px"
                    }}
                >
                    <h3 className="text-center" style={{ paddingTop: "10%" }} > {title} </h3>
                    <img src={img} style={{ width: "100%", height: "75%", paddingTop: "20px" }} alt="" />
                </FrontSide>
                <BackSide
                    style={{
                        padding: "0px",
                        margin: "0px",
                        borderRadius: "20px"
                    }}
                >
                    <h5 style={{ paddingTop: "10%"}}> {par} </h5>
                    <div style={{ float: 'left', position:"absolute",bottom:"30px",padding:"8px" }}>
                        <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round" className="feather feather-mail">
                            <path d="M4 4h16c1.1 0 2 .9 2 2v12c0 1.1-.9 2-2 2H4c-1.1 0-2-.9-2-2V6c0-1.1.9-2 2-2z"></path>
                            <polyline points="22,6 12,13 2,6"></polyline>
                        </svg> <br />
                        <a href="/" style={{color:"red",textDecoration:"none"}}>send us an email</a>
                    </div>
                    <div style={{ float: 'right', position:"absolute",bottom:"30px",right:"0" ,padding:"8px"}}>
                        <svg className="feather feather-phone" xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round" >
                            <path d="M22 16.92v3a2 2 0 0 1-2.18 2 19.79 19.79 0 0 1-8.63-3.07 19.5 19.5 0 0 1-6-6 19.79 19.79 0 0 1-3.07-8.67A2 2 0 0 1 4.11 2h3a2 2 0 0 1 2 1.72 12.84 12.84 0 0 0 .7 2.81 2 2 0 0 1-.45 2.11L8.09 9.91a16 16 0 0 0 6 6l1.27-1.27a2 2 0 0 1 2.11-.45 12.84 12.84 0 0 0 2.81.7A2 2 0 0 1 22 16.92z"></path>
                        </svg>
                        <br />
                        <a href="/" style={{color:"red",textDecoration:"none"}}>call us today</a>
                    </div>
                </BackSide>
            </Flippy>
        </>
    )
}
export default Flipcard

