import React from 'react'
import { useMediaQuery } from 'react-responsive'
export const Casecard = (props) => {
    const img = (props.img)?props.img : ""
    const par = (props.par)?props.par : "Par"
    const Desktop = ({ children }) => {
        const isDesktop = useMediaQuery({ minWidth: 992 })
        return isDesktop ? children : null
      }
      const Tablet = ({ children }) => {
        const isTablet = useMediaQuery({ minWidth: 768, maxWidth: 991 })
        return isTablet ? children : null
      }
      const Mobile = ({ children }) => {
        const isMobile = useMediaQuery({ maxWidth: 991 })
        return isMobile ? children : null
      }
      const Default = ({ children }) => {
        const isNotMobile = useMediaQuery({ minWidth: 768 })
        return isNotMobile ? children : null
      }
    return (
        <div style={{ position:"relative" , background:"rgb(245, 244, 244)" , borderRadius:"20px",margin:"32px" }}>
            <Default>
            <img src={img} style={{ width:"100%", borderRadius:"20px"}} alt="" />
            <p style={{fontSize:"1.2em" , paddingTop:"16px" ,width:"600px"}}>{par}</p>
            </Default>
            <Mobile>
            <img src={img} style={{ width:"400px", borderRadius:"20px"}} alt="" />
            <p style={{fontSize:"1.2em" , paddingTop:"16px" , width:"400px"}}>{par}</p>
            </Mobile>
            
            {/*
            <div style={{height:"8px",width:"470px",background:"#f1cb2f",position:"absolute",bottom:"0",borderRadius:"20px" , marginLeft:"15px"}} ></div>
            */}
            </div>
    )
}
export default Casecard