import React,  { useState }from 'react'
import Popup from 'reactjs-popup';
import { useMediaQuery } from 'react-responsive'
import 'reactjs-popup/dist/index.css';
import Card from './Card';
import './Modal.css'

import img1 from '../assets/img/cases/sas.jpg'
import img2 from '../assets/img/cases/thomascook.jpg'
import img3 from '../assets/img/cases/actic.jpg'
import img4 from '../assets/img/cases/pricerunner.jpg'

import Casecard from './Casecard';



export const Cases = () => {
    {/*const ref = useRef();
    const openTooltip = () => ref.current.open();
    const closeTooltip = () => ref.current.close();
    const toggleTooltip = () => ref.current.toggle();
    */}
    const [open, setOpen] = useState(false);
    const closeModal = () => setOpen(false);
    const [open1, setOpen1] = useState(false);
    const closeModal1 = () => setOpen1(false);
    const [open2, setOpen2] = useState(false);
    const closeModal2 = () => setOpen2(false);
    const [open3, setOpen3] = useState(false);
    const closeModal3 = () => setOpen3(false);
    const h1 = ["The story"]
    const h2 = ["The challenge"]
    const h3 = ["The result"]
    const p1 = ["Slagkryssaren has helped Scandinavia’s largest airline carrier, SAS, with technological development of the SAS mobile applications for Android and iPhone. We are very proud and happy to work closely with so many talented and forward thinking people at SAS"
        , "Thomas Cook turned to Slagkryssaren for help with technical development of an ambitious companion app for their 1.5 million Nordic customers. The companion app will make the travel experience more seamless and rewarding by collecting all the information the traveler might need in one place."
        , "A fitness centre brand with over 200 000 members spread across five European nations needed to enhance the customer experience. They had develop two mobile applications to help their customers stay motivated, set goals and encourage them to find new ways to exercise. The objective was to significantly improve the performance and technical stability of their applications and associated backend."
        , "PriceRunner, a leading shopping comparison site running numerous search queries every day to help their users to find the right product, for the right price and from the right store. Slagkryssaren was asked to make a total redesign of their website and implement a more intelligent search engine to attract more users and cope with the continuous increase of online shoppers."
    ]
    const p2 = ["The big challenge has been to make sure that the mobile applications integrates well with the IT-infrastructure at SAS. Both the Android and iPhone app offer a full set of features, including: booking, checkin and flightstatus. A lot of thought has gone into making these features as seamless and easy to use as possible. We have worked hard to meet the high requirements of developing applications of this magnitude. To reach SAS and our own high quality standards, we have carefully followed best practices when it comes to factors such as security and reliability."
        , "With the companion app the traveler can do a wide variety of tasks: see flight, airport transfer and payment details, get hotel and destination information and receive updates about the weather forecast. The companion app also uses notifications to alert the traveler about important dates and happenings before the trip begins.One of the biggest challenges has been to integrate all of the functionality into a coherent application that works both online and offline. We have worked closely with the talented teams at Thomas Cook to make sure that great results were achieved with both the iOS and Android application."
        , "We took over the development when the previous teams were phased out. The code base was large and time schedule short, this required high awareness and responsiveness. The main objective was to attract more users and incorporate all features from their web-app. To achieve this we needed to balance improvement to the quality of the existing code base with implementation of new functionality."
        , "The biggest challenges has been to structure and manage the redesign of the entire website to cope with the high usability expectations in combination with adapting it to already existing implementations. Additionally, implementing the optimized search engine alongside managing communication and collaboration with the company’s development team in China further enhanced the complexity of the project."
    ]
    const p3 = ["The application has become an important business component within the SAS ecosystem and has to date reached over 900 000 downloads. The application was also nominated for the Grand Travel Awards 2014."
        , "The companion app has been released on the App Store and Google Play. Both Slagkryssaren and Thomas Cook are very proud of the results that we achieved together and look forward to continued collaboration."
        , "Our customer was pleased that we in such short time could take over the project, increase code quality while adding features at a pace they needed. The previously unstable and slow development process picked up speed and gave predictable and plannable results across all functionalities."
        , "The complete redesign of the website in combination with the new improved search engine was released on time. Our customer was very pleased with the responsiveness of the new website that operates perfectly on desktop computers, tablets, and phones. They were impressed by our team’s ability to deliver the project objectives on time."
    ]
    const par = ["Business critical applications for Scandinavia's largest airline"
        , "Improving the travel experience for the customers of Thomas Cook"
        , "Mobile app for gym members at Actic"
        , "Products comparison with PriceRunner"
    ]
    const img = [img1, img2, img3, img4]
    const Desktop = ({ children }) => {
        const isDesktop = useMediaQuery({ minWidth: 992 })
        return isDesktop ? children : null
      }
      const Tablet = ({ children }) => {
        const isTablet = useMediaQuery({ minWidth: 768, maxWidth: 991 })
        return isTablet ? children : null
      }
      const Mobile = ({ children }) => {
        const isMobile = useMediaQuery({ maxWidth: 991 })
        return isMobile ? children : null
      }
      const Default = ({ children }) => {
        const isNotMobile = useMediaQuery({ minWidth: 768 })
        return isNotMobile ? children : null
      }
    return (
        <div style={{ paddingBottom: "32px" }}>

            {/*<button type = "button" className = "button" onClick = { openTooltip } > open </button>*/}
            <div className="row">
                <div className="col-xl-6 col-lg-6 col-md-6 col-sm-6">
                    {/*causeprint()*/}
                    <Desktop>
                    <a onClick={() => setOpen(o => !o)} style={{ display: "inline-block" }}>
                        <Casecard img={img1} par={par[0]} />
                        <Popup open={open} closeOnDocumentClick >
                            <Card img={img1} h1={h1[0]} h2={h2[0]} h3={h3[0]} p1={p1[0]} p2={p2[0]} p3={p3[0]} close={closeModal} />
                        </Popup >
                    </a>
                    </Desktop>

                    <Mobile>
                    <a style={{ display: "inline-block" }}>
                        <Casecard img={img1} par={par[0]} />
                    </a>
                    </Mobile>
                </div>
                <div className="col-xl-6 col-lg-6 col-md-6 col-sm-6">
                <Desktop>
                    <a onClick={() => setOpen1(o => !o)} style={{ display: "inline-block" }}>
                        <Casecard img={img2} par={par[1]} />
                        <Popup open={open1} closeOnDocumentClick className="popup-content" >
                            <Card img={img2} h1={h1[0]} h2={h2[0]} h3={h3[0]} p1={p1[1]} p2={p2[1]} p3={p3[1]} close={closeModal1} />
                        </Popup >
                    </a>
                </Desktop>
                    <Mobile>
                    <a  style={{ display: "inline-block" }}>
                        <Casecard img={img2} par={par[1]} />
                        </a>
                    </Mobile>
                </div>
            </div>

            <div className="row">
                <div className="col-xl-6 col-lg-6 col-md-6 col-sm-6">
                <Desktop>
                    <a onClick={() => setOpen2(o => !o)} style={{ display: "inline-block" }}>
                        <Casecard img={img3} par={par[2]} />
                       
                        <Popup open={open2} closeOnDocumentClick className="popup-content" >
                            <Card img={img3} h1={h1[0]} h2={h2[0]} h3={h3[0]} p1={p1[2]} p2={p2[2]} p3={p3[2]} close={closeModal2} />
                        </Popup >
                        
                    </a>
                    </Desktop>
                    <Mobile>
                    <a  style={{ display: "inline-block" }}>
                        <Casecard img={img3} par={par[2]} />
                    </a>
                    </Mobile>
                </div>
                <div className="col-xl-6 col-lg-6 col-md-6 col-sm-6">
                    <Desktop>
                    <a onClick={() => setOpen3(o => !o)} style={{ display: "inline-block" }}>
                        <Casecard img={img4} par={par[3]} />
                        
                        <Popup open={open3} closeOnDocumentClick  className="popup-content" >
                            <Card img={img4} h1={h1[0]} h2={h2[0]} h3={h3[0]} p1={p1[3]} p2={p2[3]} p3={p3[3]} close={closeModal} />
                        </Popup >
                    </a>
                    </Desktop>
                    <Mobile>
                    <a style={{ display: "inline-block" }}>
                        <Casecard img={img4} par={par[3]} />
                    </a>
                    </Mobile>
                </div>
            </div>
        </div>
    )
}
export default Cases